import logo from './logo.svg';
import './App.css';
import Button from './components/UI/Button/Button'
import Card from './components/UI/Card/Card'

function App() {
  const qotdHandler = () => {
    console.log("button clicked!!!");
		  fetch('https://favqs.com/api/qotd').then(function (response) {
			// The API call was successful!
			return response.json();
		}).then(function (data) {
			// This is the JSON from our response
			console.log(data.quote.body);
		   document.getElementById("qotd").innerHTML = data.quote.body
		}).catch(function (err) {
			// There was an error
			console.warn('Something went wrong.', err);
		});
  }

  return (
    <div className="App">
      <header className="App-header">
        <img src={logo} className="App-logo" alt="logo" />
        <p>
          Mohammed Moiyadi - Edit <code>src/App.js</code> and save to reload.
        </p>
        <a
          className="App-link"
          href="https://reactjs.org"
          target="_blank"
          rel="noopener noreferrer"
        >
          Learn React
        </a>
        <Card>
          <Button onClick={qotdHandler}>Quote of the day</Button>
          <p id="qotd" placeholder="Quote of the day">sd</p> 
        </Card>
        
      </header>
      
    </div>
  );
}

export default App;
